/*********************************
    ヘッダー
*********************************/
.header__inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px;
}
.header__title {
  font-size: 2rem;
}

/*********************************
    フッター
*********************************/
.footer {
  position: sticky;
  top: 100vh;
  width: 100%;
  background-color: #eeeeee;
}
.footer__inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 10px;
}
.footer__text {
  font-size: 0.8rem;
}

/*********************************
    レイアウト
*********************************/
/* ラッパー */
/* コンテナ */
.container--bg-gray {
  background-color: #fafafa;
}
.container__inner {
  max-width: 1200px;
  margin: 0 auto;
  padding: 40px 0;
}
.container:first-child .container__inner {
  padding: 20px 0 40px;
}

/*********************************
    コンテンツ
*********************************/
/* コンテンツ */
.content__inner {
  margin: 0 20px 40px;
}
.content:last-child .content__inner {
  margin: 0 20px 0px;
}

/* ボックス */
.box {
  background-color: #ffffff;
  border: 1px solid #eeeeee;
  border-radius: 4px;
  box-shadow: 0 2px 8px 0 rgba(0, 0, 0, 0.0862745098);
}
.box__inner {
  padding: 20px;
}
.box__title {
  font-size: 1.4rem;
  font-weight: bold;
}

/* ヘッダー */
/* フッター */
/* レイアウト */
/* コンテンツ */
/*********************************
    トップページ
*********************************/
.weather .box--blue {
  background-color: #F7F9FB;
}
.weather .forecast {
  max-width: 1080px;
  margin: 0 auto;
  font-family: noto-sans-cjk-jp, sans-serif;
  font-size: 16px;
  line-height: 1.4;
  color: #333333;
}
.weather .forecast__title {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 28px;
  font-weight: bold;
}
.weather .forecast__bar {
  width: 80px;
  height: 8px;
  margin-top: 21px;
  border-radius: 100px;
  background: linear-gradient(to right, #15D6E6, #05A9F4, #005BBE);
}
.weather .forecast__content {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-top: 32px;
}
@media screen and (min-width: 901px) {
  .weather .forecast__content {
    flex-direction: row;
    gap: 16px;
  }
}
.weather .forecast__day {
  background-color: #FFFFFF;
  border-radius: 7px;
  overflow: hidden;
}
@media screen and (min-width: 901px) {
  .weather .forecast__day--today {
    width: calc(60% - 7px);
  }
}
@media screen and (min-width: 901px) {
  .weather .forecast__day--tomorrow {
    width: 40%;
  }
}
.weather .forecast__day--week {
  background-color: #F7F9FB;
}
@media screen and (min-width: 901px) {
  .weather .forecast__day--week {
    flex: 1;
  }
}
.weather .forecast__head {
  padding: 21px;
  text-align: center;
  font-size: 20px;
  font-weight: bold;
}
.weather .forecast__head--today {
  background-color: #E7F0FC;
  color: #4A90E2;
}
.weather .forecast__head--tomorrow {
  background-color: #E4E4E4;
}
.weather .forecast__head--week {
  background-color: #E7F0FC;
  color: #4A90E2;
}
.weather .forecast__data {
  padding: 24px;
  color: #424242;
}
.weather .forecast__condition {
  display: flex;
  justify-content: center;
  font-size: 16px;
}
.weather .forecast__image {
  display: flex;
  justify-content: center;
  margin-top: 18px;
}
.weather .forecast__temp {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-top: 36px;
}
.weather .forecast .temp {
  text-align: center;
}
.weather .forecast .temp__celsius {
  font-size: 20px;
  font-weight: bold;
}
.weather .forecast .temp__between {
  font-size: 16px;
  margin-top: 4px;
}
.weather .forecast .temp--max {
  color: #FF0000;
}
.weather .forecast .temp--min {
  color: #4A90E2;
}
.weather .forecast__precip {
  max-width: 360px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  gap: 8px;
  margin-top: 23px;
}
.weather .forecast .precip {
  text-align: center;
  flex: 1;
  font-size: 16px;
}
.weather .forecast .precip__volume {
  margin-top: 2px;
}

